import { DrawingData, PartData } from '../types';

const validatePost = (drawing: DrawingData, parts: PartData[]) => {
  if (drawing.name === '') {
    alert(' .');
    return false;
  } else if (drawing.client < 0) {
    alert('  .');
    return false;
  }

  if (parts.length < 1) {
    alert('  1 .');
    return false;
  }

  for (let i = 0; i < parts.length; i++) {
    if (parts[i].division < 0) {
      alert(`${i + 1}   .`);
      return false;
    } else if (parts[i].x === '') {
      alert(`${i + 1}  X .`);
      return false;
    } else if (parts[i].y === '') {
      alert(`${i + 1}  Y .`);
      return false;
    } else if (parts[i].z === '') {
      alert(`${i + 1}  Z .`);
      return false;
    } else if (parts[i].material === '') {
      alert(`${i + 1}   .`);
      return false;
    } else if (parts[i].quantity <= 0) {
      alert(`${i + 1}   .`);
      return false;
    }
  }

  return true;
};

export default validatePost;
